<template>
   <div class="examSec">
	  <!-- <div class="vback" @click="back()">
	   	<img src="../../../../assets/img/backh.png" >返回
	   </div> -->
	   <!-- <examPSec :allItems="allItems"></examPSec>
	   <examRslid :allItems="allItems"></examRslid> -->
	   <exam :allItems="allItems" :time="time" :id="id"></exam>
   </div>
</template>

<script>

import examPSec from '../../../../components/secCommon/examPSec.vue'
import examRslid from '../../../../components/secCommon/examRslid.vue'
import exam from '../../../../components/secCommon/exam.vue'
import {getEaxmInfo} from '../../../../api/apis/user.js'
import {clearData} from '../../../../utils/utils.js'
export default {
	name:'examSec',
	data(){
		return {
			allItems:{},
			time:0,
			id:''
		}
	},
	components:{
		examPSec,
		exam,
		examRslid
	},
	mounted() {
		this.id=this.$route.query.id
		let that=this
		this.time=this.$route.query.time
		getEaxmInfo({id:this.id}).then(res=>{
			that.allItems=res.data.response
		})
	},
    methods: {
     back(){
		 clearData()
		 this.$router.push('/student/exam')
	 }
    }
}
</script>

<style scoped>
@import '../../../../assets/css/examSec.css';
</style>



