<template>
   <el-row class="examRadio">
     <el-col :span="24">
		 <el-col :span="3">
			 <div class="radioT">
			 	单选题
			 </div>
		 </el-col>
		 <el-col :span="10" class="radioT2">
		 	{{rlist.itemOrder}}{{rlist.title}}1.发票的日期在结算单位单据中应( ).
			<span class="rnum">(2分)</span>
		 </el-col>
	 </el-col>
	 <el-col :span="24">
		 <el-col :span="15" :offset="3" class="rcom">
		 <el-radio-group v-model="radio" @change="change">
			 <div class="rcom" v-for="(item,index) in rlist.items" :key="index">
				<el-radio :label="index">{{item.prefix}}.{{item.content}}</el-radio>
			 </div>
		 </el-radio-group>
		 </el-col>
	 </el-col>
   </el-row>
</template>

<script>
export default {
	name:'examRadio',
	data(){
		return {
			radio:'',
			rlist:''
		}
	},
	props:['list'],
	mounted() {
	},
	methods:{
		change(val){
			console.log(val)
		}
	},
	watch:{
		'list':function(val) {
			this.rlist=val
		}
	}
}
</script>

<style scoped lang="less">
@import '../../assets/css/examRadio.css';
</style>
<style type="text/css">
	@import '../../assets/css/examRadioA.css';
</style>


