<template>
   <el-row class="exam">
     <el-col :span="24">
		 <el-col :span="24" class="title">
			 <el-col :span="10" class="examTitle">
			 	{{list.name}}
			 </el-col>
			 <el-col :span="5" :offset="2">
				 <div class="time">
				 	<!-- <i class="el-icon-alarm-clock"></i> -->
					 <span>{{h}}:</span>
														<span>{{m}}:</span>
														<span>{{s}}</span>
				 </div>
			 </el-col>
			 <el-col :span="5">
				 总分：{{list.score}}分
			 </el-col>
		 </el-col>
		 <el-col :span="24" v-for="(item,index) in list.titleItems" :key="index">
			 <el-col>
			 			 <div class="radioT">
			 			 	{{item.name}}
			 			 </div>
						
			 </el-col>
			 <el-col class="totalT">
			  <div>（共{{item.questionItems.length}}小题）</div>
			  </el-col>
		<!-- 	 <el-col :span="5">
			 <div class="grid-content"></div>
			 </el-col> -->
			 <examItems :items="item" :newList="newList" :answor="answor" :open="open"></examItems>
		 </el-col>
		 <el-col class="sub">
		 		 <el-button type="primary" @click="handIn">交卷</el-button>
		 </el-col>
		 <el-dialog
		   title="提示"
		   :visible.sync="open"
		   width="30%"
		   center
		   :before-close="handleClose">
		   <el-row>
		   			   <el-col>{{sendTitle}}</el-col>
		   			   <!-- <el-col>应答题目：<span class="cortext">{{maxL}}</span>题</el-col>
		   			   <el-col>完成题目：<span class="cortext" :class="{redtext:sendTitle=='您还有题目未完成！'}">{{totalNum}}</span>题</el-col> -->
		   			   <!-- <el-col>考试用时：<span class="cortext">{{h}}:{{m}}:{{s}}</span></el-col> -->
		   			   <el-col>交卷请按”确定“；返回答题界面，请按”取消“。</el-col>
		   </el-row>
		<!--   <el-row>
			   <el-col>{{sendTitle}}</el-col>
			   <el-col>应答题目：<span class="cortext">{{maxL}}</span>题</el-col>
			   <el-col>完成题目：<span class="cortext" :class="{redtext:sendTitle=='您还有题目未完成！'}">{{totalNum}}</span>题</el-col>
			   <el-col>考试用时：<span class="cortext">{{h}}:{{m}}:{{s}}</span></el-col>
			   <el-col>交卷请按”确定“；返回答题界面，请按”取消“。</el-col>
		  </el-row> -->
		   <span slot="footer" class="dialog-footer">
		     <el-button @click="open = false">取 消</el-button>
		     <el-button type="primary" @click="isSend">确 定</el-button>
		   </span>
		 </el-dialog>
	 </el-col>
   </el-row>
</template>

<script>
import {getDate,isCompleted,getMax,fixData,enditJ,getL,overT,clearData,getAllItems,saveD,getDate1} from '../../utils/utils.js'
import {getR} from '../../utils/examUtils.js'
import examRadio from '../exam/examRadio.vue'
import examtitle from '../exam/examtitle.vue'
import examItems from '../exam/examItems.vue'
import {subPage} from '../../api/apis/user.js'
let now
export default {
	name:'exam',
	data(){
		return {
			list:{titleItems:[]},
			nowTime:0,
			h:"00",
			s:"00",
			m:"00",
			nowT:0,
			nowR:{},
			maxL:0,
			nowTitle:'单选题',
			fixD:[],
			answor:[],
			open:false,
			ranswors:[],
			danswors:[],
			panswors:[],
			janswors:[],
			totalNum:0,
			sendTitle:'',
			totalTime:'',
			usedTime:0,
			allItem:{
				answerItems:[],
				"doTime": 0,
				"id": 0,
				"score": ""
			},
			newList:[]
			
		}
	},
	props:[
		'allItems',
		'time',
		'id'
	],
	mounted() {
		let that=this
	
		now = setInterval(function(){
			  // that.h=getDate(that.nowTime)[1]
			  // that.m=getDate(that.nowTime)[2]
			  // that.s=getDate(that.nowTime)[3]
			  // that.nowTime=getDate(that.nowTime)[0]
			  that.h=getDate1(that.totalTime)[1]
			  that.m=getDate1(that.totalTime)[2]
			  that.s=getDate1(that.totalTime)[3]
			  that.totalTime=getDate1(that.totalTime)[0]
			  that.usedTime++
			  
			  if(!overT(that.totalTime)){
				  clearInterval(now)
				  that.$router.go(-1)
			  }
		},1000)
	
	},
	components:{
		examRadio,
		examtitle,
		examItems
	},
	methods:{
		goClass(){
			clearData()
			// 我的考试
			this.$router.push('/student/exam')
		},
		start(){
		},
		// 上一题，下一题
		go(flag){
			
		},
		handIn(){
			clearTimeout(now)
			this.open=!this.open
			this.handleClose()
			
		},
		handleClose(){
			// 单选
			this.ranswors=enditJ(localStorage.getItem('ranswors'))
			// 多选
			this.danswors=enditJ(localStorage.getItem('danswors'))
			// 判断
			this.panswors=enditJ(localStorage.getItem('panswors'))
			// 简答
			this.janswors=enditJ(localStorage.getItem('janswors'))
			
			this.allItem.doTime=this.usedTime
			this.allItem.id=this.id
		
			this.allItem.answerItems=enditJ(localStorage.getItem('newList'))
			// this.allItem.answerItems=getAllItems(this.ranswors,this.danswors,this.panswors,this.janswors,this.list)
			this.getMaxL()
		},
		// 提交答案
		isSend(){
			this.handleClose()
			subPage(this.allItem).then(res=>{
				console.log(res)
			})
			// localStorage.setItem("isSen",parseJ({pageid:this.id}))
			this.open = false
			// this.$router.go(-1)
			clearData()
			this.goClass()
		},
		getMaxL(){
			let l1=getL(this.ranswors)
			let l2=getL(this.danswors)
			let l3=getL(this.panswors)
			let l4=getL(this.janswors)
			this.totalNum=l1+l2+l3+l4
			if(this.totalNum==this.maxL){
				this.sendTitle="恭喜您，完成全部题目！"
			} else{
				this.sendTitle="您还有题目未完成！"
			}
		}
	},
	watch:{
		'allItems':function(val){
			// this.list=this.allItems
			this.list=fixData(this.allItems)[1]
			this.fixD=fixData(this.allItems)[0]
			this.answor=fixData(this.allItems)[2]
			this.maxL=getMax(this.list.titleItems)
			saveD(this.list)
			this.list.titleItems.forEach(item=>{
				item.questionItems.forEach(item=>{
					item.completed=false
					item.questionId=item.id
					item.contentArray=[]
					this.newList.push(item)
				})
			})
		},
		'time':function(val){
			this.totalTime=this.time*60
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/exam.css';
</style>



