<template>
   <el-row class="examRadio">
	<!--   <el-col :span="3">
	   			 <div class="radioT">
	   			 	单选题
	   			 </div>
	   </el-col> -->
     <el-col :span="24" v-show="rlist.name=='单选题'" v-for="(item,index) in rlist.questionItems" :key="index">
		 <el-col :span="12" class="radioT2" :offset="3" >
		 	{{index+1}}.{{item.title}}<span class="rnum">({{item.score}}分)</span>
			<!-- <span class="rnum">(2分)</span> -->
		 </el-col>
		 <el-col :span="24">
		 	<el-col :span="15" :offset="3" class="rcom" >
		 	<el-radio-group v-model="ranswors.list[index].answor" @change="change">
		 				<div class="rcom options" style="display: inline-block;" v-for="(item2,index2) in item.items" :key="index2">
		 					<el-radio :label="item2.nowId">{{item2.nowContent}}</el-radio>
		 				 </div>
		 	</el-radio-group>
		 	</el-col>
		 </el-col>
	 </el-col>
	 <el-col :span="24" v-show="dlist.name=='多选题'" v-for="(item,index) in dlist.questionItems" :key="index">
	 		 <el-col :span="10" class="radioT2" :offset="3" >
	 		 	{{index+1}}.{{item.title}}<span class="rnum">({{item.score}}分)</span>
	 		 </el-col>
	 		 <el-col :span="24">
	 		 	<el-col :span="15" :offset="3" class="rcom" >
					<el-checkbox-group v-model="danswors.list[index].answor" @change="handleChecked">
						<div class="rcom options" style="display: inline-block;" v-for="(item2,index2) in item.items" :key="index2">
							<el-checkbox :label="item2.nowId">{{item2.nowContent}}</el-checkbox>
						</div>
					 </el-checkbox-group>
	 		 	</el-col>
	 		 </el-col>
	 </el-col>
	 <el-col :span="24" v-show="Plist.name=='判断题'" v-for="(item,index) in Plist.questionItems" :key="index">
	 		 <el-col :span="10" class="radioT2" :offset="3" >
	 		 	{{index+1}}.{{item.title}}<span class="rnum">({{item.score}}分)</span>
	 			<!-- <span class="rnum">(2分)</span> -->
	 		 </el-col>
	 		 <el-col :span="24">
	 		 	<el-col :span="15" :offset="3" class="rcom" >
	 		 	<el-radio-group v-model="panswors.list[index].answor" @change="pchange">
	 		 				<div class="rcom options" style="display: inline-block;" v-for="(item2,index2) in item.items" :key="index2">
	 		 					<el-radio :label="item2.nowId">{{item2.nowContent}}</el-radio>
	 		 				 </div>
	 		 	</el-radio-group>
	 		 	</el-col>
	 		 </el-col>
	 </el-col>
	 <el-col :span="24" v-show="jlist.name=='简答题'" v-for="(item,index) in jlist.questionItems" :key="index">
	 		 <el-col :span="10" class="radioT2" :offset="3" >
	 		 	{{index+1}}.{{item.title}}<span class="rnum">({{item.score}}分)</span>
	 			<!-- <span class="rnum">(2分)</span> -->
	 		 </el-col>
	 		 <el-col :span="24">
	 		 	<el-col :span="21" :offset="3" class="rcom" >
	 		 	<el-input
	 		 	  type="textarea"
	 		 	  :rows="2"
	 		 	  placeholder="请输入答案"
				  @change="dchange"
	 		 	  v-model="janswors.list[index].answor">
	 		 	</el-input>
	 		 	</el-col>
	 		 </el-col>
	 </el-col>
	

   </el-row>
</template>

<script>
import {sData,saData,parseJ,pData} from '../../utils/utils.js' 
export default {
	name:'examRadio',
	data(){
		return {
			radio:'',
			rlist:[],
			dlist:[],
			Plist:[],
			checkList:[],
			jlist:[],
			jd:'',
			ranswors:{list:[],'name':"单选题",key:'1'},
			danswors:{list:[],'name':"多选题",key:'2'},
			panswors:{list:[],'name':"判断题",key:'3'},
			janswors:{list:[],'name':"简答题",key:'4'},
			opend:false,
			arr:[]
		}
	},
	props:[
		'items',
		'open',
		'newList'
	],
	mounted() {
		// console.log("N",this.items)
		if(this.items.name=="单选题"){
			this.rlist=this.items
			this.ranswors.list=sData(this.items)
		}else if(this.items.name=="多选题"){
			this.dlist=this.items
			this.danswors.list=saData(this.items)
		}else if(this.items.name=="判断题"){
			this.Plist=this.items
			this.panswors.list=sData(this.items)
		}else if(this.items.name=="简答题"){
			this.jlist=this.items
			this.janswors.list=sData(this.items)
		}
		this.arr=pData(this.newList)
		this.arr.forEach(item=>{
				item.contentArray=[]
				item.content=''
		})
		localStorage.setItem("newList",parseJ(this.arr))
	},
	methods:{
		// 单选
		change(val){
			localStorage.setItem("ranswors",parseJ(this.ranswors))
			// console.log(this.newList)
			this.arr=JSON.parse(localStorage.getItem("newList"))
			this.arr.forEach(item=>{
				if(val.split('.')[0]==item.id){
					item.questionId=item.id
					item.completed=true
					if(val.split('.')[1]==0){
						item.content='A'
					}
					if(val.split('.')[1]==1){
						item.content='B'
					}
					if(val.split('.')[1]==2){
						item.content='C'
					}
					if(val.split('.')[1]==3){
						item.content='D'
					}
				}
			})
			localStorage.setItem("newList",parseJ(this.arr))
		},
		// 多选
		handleChecked(val){
			
			localStorage.setItem("danswors",parseJ(this.danswors))
			this.arr=JSON.parse(localStorage.getItem("newList"))
			// console.log(this.newList)
			this.arr.forEach(item=>{
				val.forEach(ite=>{
					if(ite.split('.')[0]==item.id){
						item.questionId=item.id
						item.completed=true
						if(ite.split('.')[1]==0){
							item.contentArray.push('A')
						}
						if(ite.split('.')[1]==1){
							item.contentArray.push('B')
						}
						if(ite.split('.')[1]==2){
							item.contentArray.push('C')
						}
						if(ite.split('.')[1]==3){
							item.contentArray.push('D')
						}
						if(ite.split('.')[1]==4){
							item.contentArray.push('E')
						}
						if(ite.split('.')[1]==5){
							item.contentArray.push('F')
						}
					}
				})
				
			})
			// console.log("多选",this.arr)
			localStorage.setItem("newList",parseJ(this.arr))
		},
		// 判断
		pchange(val){
			localStorage.setItem("panswors",parseJ(this.panswors))
			this.arr=JSON.parse(localStorage.getItem("newList"))
			// console.log(this.newList)
			this.arr.forEach(item=>{
				if(val.split('.')[0]==item.id){
					item.questionId=item.id
					item.completed=true
					if(val.split('.')[1]==0){
						item.content='A'
					}
					if(val.split('.')[1]==1){
						item.content='B'
					}
				}
			})
			localStorage.setItem("newList",parseJ(this.arr))
		},
		// 简答
		dchange(val){
			this.arr=JSON.parse(localStorage.getItem("newList"))
			localStorage.setItem("janswors",parseJ(this.janswors))
			// console.log(this.newList)
			// console.log("简答题：",this.arr)
			this.arr.forEach(item=>{
				// console.log("简答题：",item)
				this.janswors.list.forEach(ite=>{
					if(item.questionId==ite.id){
						item.questionId=item.id
						item.completed=true
						item.content=val
					}
				})
				// console.log(val)
				// if(val.split('.')[0]==item.id){
				// 	item.questionId=item.id
				// 	item.completed=true
				// 	item.content=val
				// }
			})
			// console.log(this.arr)
			localStorage.setItem("newList",parseJ(this.arr))
		},
		
	},
	watch:{
		'open':function(val){
			this.opend=val
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/examRadioN.css';
</style>



