<template>
   <el-row class="examPSec">
     <el-col :span="24">
		 <el-col :span="24" class="title">
			<!-- <el-col :span="2">
				 第一章
			 </el-col> -->
			 <el-col :span="10" class="examTitle">
			 	{{list.name}}
			 </el-col>
			 <el-col :span="5" :offset="6">
				 <div class="time">
				 	<i class="el-icon-alarm-clock"></i> <span>{{h}}:</span>
														<span>{{m}}:</span>
														<span>{{s}}</span>
				 </div>
			 </el-col>
		 </el-col>
		<!-- <el-col :span="24" class="title2">
		 		单选题共12题,每道题2分.
		 </el-col> -->
		 <examtitle></examtitle>
		 <examRadio :list="nowR"></examRadio>
	<!-- 	 <el-divider class="eline"></el-divider>
		 <examRadio></examRadio> -->
		 <el-col :span="24" class="cpage">
			 <el-button type="primary" @click="go('1')">上一题</el-button>
			 <el-button type="primary" @click="go('2')">下一题</el-button>
		 </el-col>
		<!-- <el-col :span="24" class="cpage">
			 <el-button type="primary">提交</el-button>
		 </el-col> -->
	 </el-col>
   </el-row>
</template>

<script>
import {getDate,isCompleted,getMax} from '../../utils/utils.js'
import {getR} from '../../utils/examUtils.js'
import examRadio from '../exam/examRadio.vue'
import examtitle from '../exam/examtitle.vue'
let now
export default {
	name:'examPSec',
	data(){
		return {
			list:[],
			nowTime:0,
			h:"00",
			s:"00",
			m:"00",
			nowT:0,
			nowR:{},
			maxL:0,
			nowTitle:'单选题'
		}
	},
	props:[
		'allItems'
	],
	mounted() {
		let that=this
		now = setInterval(function(){
			  that.h=getDate(that.nowTime)[1]
			  that.m=getDate(that.nowTime)[2]
			  that.s=getDate(that.nowTime)[3]
			  that.nowTime=getDate(that.nowTime)[0]
		},1000)
	},
	components:{
		examRadio,
		examtitle
	},
	methods:{
		goClass(){
			// 我的课程
			this.$router.push('/student/exam')
		},
		start(){
		},
		// 上一题，下一题
		go(flag){
			let isc=isCompleted(flag,this.nowT,this.list,this.maxL,this.nowTitle)
			if(!isc){
				return
			}
			getR()
			this.nowT=isc[0]
			this.nowR=isc[1]
			console.log("isc",isc,this.maxL)
		}
	},
	watch:{
		'allItems':function(val){
			this.list=this.allItems
			this.maxL=getMax(this.list.titleItems)
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/examPSec.css';
</style>



